    background-color: #7BB5EC;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #9fc9f1;
  }
}
.circle-icon {
  position: relative;
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 50%;
  border: 1px solid #D8E5F1;
}
.circle-icon svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.125rem;
  height: 1.125rem;
}

.call-socials {
  display: inline-block;
  vertical-align: middle;
  width: 1.5rem;
  height: 1.5rem;
}
.call-socials-wats {
  width: 1.25rem;
  height: 1.25rem;
}
.call-socials-telegram {
  width: 1.375rem;
  height: 1.375rem;
}
.call-socials:not(:last-child) {
  margin-right: 0.0625rem;
}

.page-title {
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  color: #3CA3CE;
  text-align: center;
  font-family: "Philosopher";
  font-size: 64px;
  font-size: 4rem;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  padding-left: 36px;
  padding-right: 36px;
  margin-top: 40px;
  margin-bottom: 106px;
}
.page-title-back {
  position: absolute;
  color: #C73752;
  font-family: "Inter";
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 150%;
  text-decoration-line: underline;
  display: inline-block;
  text-transform: none;
  left: 50%;
  transform: translateX(-50%);
  bottom: -31px;
  transition: color 0.3s;
}
.page-title-back:hover {
  color: #DA3C5A;
}
.page-title::before {
  position: absolute;
  top: 50%;
  right: 100%;
  transform: translateY(-50%);
  content: "";
  background-image: url(../images/title-img.svg);
  width: 190px;
  height: 6px;
  display: block;
}
.page-title::after {
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  content: "";
  background-image: url(../images/title-img.svg);
  width: 190px;
  height: 6px;
  display: block;
}

.section-title {
  color: #3CA3CE;
  font-size: 48px;
  font-size: 3rem;
  font-family: "Philosopher";
  font-weight: 700;
  text-transform: uppercase;
  user-select: none;
  margin-bottom: 3.875rem;
  text-align: center;
}
.section-title__left {
  text-align: left;
}

.ui-field label.error {
  color: #CB0000;
  font-size: 12px;
  font-size: 0.75rem;
  width: max-content;
}

.ui-input {
  border: 1px solid #fff;
  color: #fff;
  background: transparent;
  padding: 1.125rem;
  outline: none;
  border-radius: 0.375rem;
  width: 100%;
}
.ui-input::placeholder {
  color: #EAF5FF;
}

.ui-textarea {
  border: 1px solid #fff;
  color: #fff;
  background: transparent;
  padding: 1.125rem;
  outline: none;
  border-radius: 0.375rem;
  width: 100%;
  height: 104px;
  resize: none;
}
.ui-textarea::placeholder {
  color: #EAF5FF;
}

.ui-btn {
  border: none;
  color: #fff;
  outline: none;
  background-color: #C73752;
  padding: 1.125rem 2.3125rem;
  border-radius: 2.25rem;
  cursor: pointer;
  transition: background-color 0.3s;
}
.ui-btn:hover {
  background-color: #DA3C5A;
}

