.wrapper {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -45%);
  width: 100vw;
  height: 100vw;
  max-width: 120rem;
  max-height: 120rem;
  border-radius: 50%;
  background: linear-gradient(90deg, rgba(29, 114, 193, 0.0760679272) 0%, rgba(246, 251, 255, 0.7903536415) 53%, rgba(29, 114, 193, 0.1264880952) 90%);
  z-index: -1;
}

.header {
  margin-bottom: 0.625rem;
}
.header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-contacts {
  display: flex;
  align-items: flex-start;
  width: calc(50% - 195px / 2);
  padding-left: 3.375rem;
  flex-wrap: wrap;
  justify-content: flex-end;
  padding-top: 0.75rem;
}
.header-contacts-item {
  display: flex;
  gap: 6px;
}
.header-contacts-item:not(:last-child) {
  margin-right: 12px;
}
.header-contacts-text {
  white-space: nowrap;
}
.header-contacts-text:first-child {
  padding-top: 0.375rem;
}
.header-contacts-text a {
  transition: color 0.3s;
}
.header-contacts-text a:hover {
  color: #DA3C5A;
}
.header-nav {
  width: calc(50% - 195px / 2);
  padding-right: 3.375rem;
}
.header-logo {
  display: block;
  width: 12.1875rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
.header-logo svg {
  width: 12.1875rem;
  height: auto;
}
.header-icon {
  position: relative;
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 50%;
  border: 0.0625rem solid #D8E5F1;
}
.header-icon svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.125rem;
  height: 1.125rem;
}

.header-nav > ul {
  display: flex;
  justify-content: space-between;
}
.header-nav > ul > li {
  position: relative;
  white-space: nowrap;
}
.header-nav > ul > li:not(:last-child) {
  margin-right: 0.75rem;
}
.header-nav > ul > li:hover > a {
  color: #C73752;
}
.header-nav > ul > li:hover > .icon {
  transform: rotate(0deg);
  stroke: #C73752;
}
.header-nav > ul > li .icon {
  width: 1.125rem;
  height: 1.125rem;
  transform: rotate(-180deg);
  stroke: #1D1D1D;
  transition: transform 0.3s, stroke 0.3s;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}
.header-nav > ul > li > a {
  color: #1D1D1D;
  font-size: 1rem;
  font-weight: 600;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  transition: color 0.3s;
  display: inline-block;
}
.header-nav > ul > li > ul {
  position: absolute;
  width: max-content;
  left: -1.5rem;
  bottom: 0;
  transform: translateY(100%);
  display: none;
  border-radius: 0.75rem;
  background: #fff;
  box-shadow: 0 0 3.1875rem 0 rgba(26, 59, 159, 0.07);
  padding: 1.5rem;
  z-index: 2;
}
.header-nav > ul > li > ul > li:not(:last-child) {
  margin-bottom: 1.25rem;
}
.header-nav > ul > li > ul > li a {
  font-weight: 400;
}
.header-nav > ul > li > ul > li a:hover {
  color: #C73752;
}
.header-nav > ul > li:hover > ul {
  display: block;
}

.slider-container {
  position: relative;
}
.slider-container .swiper-pagination {
  bottom: 0.75rem;
}
.slider-container .swiper-pagination-bullet {
  width: 0.75rem;
  height: 0.75rem;
  background: #A4B5C5;
  margin: 0 0.375rem !important;
}
.slider-container .swiper-pagination-bullet-active {
  background: #3CA3CE;
}

.slider-wrapper {
  position: relative;
  margin-right: -0.625rem;
  margin-left: -0.625rem;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}

.slider-card {
  margin-bottom: 4.375rem;
}

.slider-next__edge {
  right: 0;
  transform: translate(50%, 50%);
  bottom: calc(50% + 35px);
}

.slider-prew__edge {
  left: 0;
  transform: translate(-50%, 50%);
  bottom: calc(50% + 35px);
}

.slider-prew, .slider-next {
  position: absolute;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  background-color: #C73752;
  z-index: 1;
  transition: background-color 0.3s;
  cursor: pointer;
}
.slider-prew:hover, .slider-next:hover {
  background-color: #DA3C5A;
}
.slider-prew .icon, .slider-next .icon {
  width: 1.25rem;
  height: 1.25rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main {
  flex-grow: 1;
}
.main-slider-slide {
  border-radius: 1rem;
}
.main-slider-slide:not(.swiper-slide-active) {
  filter: blur(1px);
}
.main-slider-card img {
  display: block;
}

.section-slider {
  margin-bottom: 6.25rem;
}
.section-slider .swiper-pagination {
  bottom: 3.125rem;
}
.section-slider .swiper-pagination-bullet {
  background-color: #fff;
  opacity: 1;
  height: 0.5rem;
  width: 0.6875rem;
  border-radius: 0.5rem;
  transition: width 0.3s;
}
.section-slider .swiper-pagination-bullet-active {
  width: 2.5rem;
}

.section-tours {
  margin-bottom: 3rem;
}

.tour-card {
  display: block;
  padding: 0.625rem;
  border-radius: 0.5rem;
  background: #FFF;
  box-shadow: 0px 11px 14px 0px rgba(187, 202, 216, 0.07), 0px 23px 13px 0px rgba(187, 202, 216, 0.09), 0px 38px 28px 0px rgba(187, 202, 216, 0.05), 0px 42px 39px 0px rgba(187, 202, 216, 0.01), 0px 89px 45px 0px rgba(187, 202, 216, 0);
}
.tour-card-image {
  border-radius: 0.25rem;
  overflow: hidden;
  aspect-ratio: 1.47;
  display: block;
}
.tour-card-image:hover img {
  transform: scale(1.1);
}
.tour-card-image img {
  display: block;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  transition: transform 0.3s;
}
.tour-card-info {
  padding: 1rem 0.5625rem 0.625rem 0.5625rem;
  height: 11.375rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.tour-card-title {
  font-weight: 600;
  line-height: 130%;
  margin-bottom: 1rem;
  -webkit-line-clamp: 3;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  min-height: 62px;
  transition: color 0.3s;
}
.tour-card-title:hover {
